html,
body {
  margin: 0;
  padding: 0;
  height: 100%; }

body {
  font-family: 'Open Sans Condensed', sans-serif;
  color: #404041;
  font-size: 20px;
  position: relative; }

a {
  color: #404041;
  text-decoration: none;
  transition: all 0.2s; }

.logo-wraps a {
  transition: none; }

a:hover {
  color: #00ADFF;
  text-decoration: none; }

img {
  max-width: 100%; }

#contact-us-btn {
  display: none; }

.contact-form-wrap {
  display: none;
  padding: 20px 0;
  text-align: center; }
  .contact-form-wrap .contact-form {
    display: inline-block; }
    .contact-form-wrap .contact-form input, .contact-form-wrap .contact-form textarea {
      width: 100%;
      border: 1px solid #00ADFF;
      box-sizing: border-box; }
    .contact-form-wrap .contact-form textarea {
      height: 121px; }
    .contact-form-wrap .contact-form input[type='text'], .contact-form-wrap .contact-form input[type='email'] {
      height: 60px;
      border-right: none; }
    .contact-form-wrap .contact-form input[type='submit'] {
      height: 60px;
      margin-top: -7px; }
    .contact-form-wrap .contact-form .forms-wrap-1 {
      float: left; }
      .contact-form-wrap .contact-form .forms-wrap-1 span:not(:last-child) input {
        border-bottom: none; }
    .contact-form-wrap .contact-form .forms-wrap-2 {
      float: right; }

header {
  margin-bottom: 20px;
  padding-top: 10px;
  border-bottom: 1px solid;
  position: relative;
  z-index: 100;
  background: #F0FAFF;
  text-align: center; }

.up {
  position: fixed;
  right: 20px;
  font-size: 30px;
  bottom: 10px;
  opacity: 0.5;
  transition: opacity 0.5s;
  cursor: pointer;
  z-index: 10; }
  .up:hover {
    opacity: 1; }

footer {
  margin: 50px 0 -30px;
  padding: 6px 0;
  border-top: 1px solid #404041;
  background: #EFFFEF;
  color: #ccc;
  position: relative; }

.footer-content-wrap {
  display: block; }

.footer-text-wrap {
  float: left;
  margin: 9px 0 0 30px; }

.footer-text-wrap p {
  display: inline-block;
  border-right: 1px solid;
  color: #868686;
  font-size: 14px;
  padding-right: 5px; }

.footer-text-wrap p:last-child {
  border-right: none; }

.menu-wrap {
  width: 100%;
  position: relative; }

.menu-wrap-2:first-child {
  margin: auto;
  position: relative;
  z-index: 1000; }

.menu-wrap-2:first-child > ul.main-menu > li > a,
.mob-menu.menu-1 ul.main-menu li a {
  margin-left: 0;
  font-size: 14px;
  position: relative;
  top: -6px;
  font-weight: normal; }

.menu-wrap-2:first-child ul.sub-menu li a,
.mob-menu.menu-1 ul.main-menu li a {
  font-size: 13px; }

.menu-wrap-2:first-child ul.main-menu ul.sub-menu a {
  font-weight: normal; }

.menu-wrap-2:nth-child(2) ul.main-menu {
  width: 82%;
  margin-top: 4px;
  padding-left: 10px;
  font-size: 16px; }
  .menu-wrap-2:nth-child(2) ul.main-menu li:first-child ul.sub-menu {
    width: 130px; }

.menu-wrap-2:nth-child(2) ul.main-menu > li {
  position: relative;
  float: left;
  margin-right: 4px; }

.menu-wrap-2 ul.main-menu > li.menu-item-has-children {
  height: 40px; }

.menu-wrap-2:nth-child(2) ul.main-menu > li > a {
  margin-left: 7px; }

.menu-wrap-2:nth-child(2) ul.main-menu > li.menu-item-has-children > a {
  margin-left: 16px; }

.menu-wrap-2:nth-child(2) li.menu-item-has-children:before {
  content: '\f100';
  position: absolute;
  top: 6px;
  left: 2px;
  font: 12px Flaticon;
  transition: color 0.2s; }

.menu-wrap-2 ul.main-menu > li:hover > a {
  color: #00ADFF; }

.menu-wrap-2:nth-child(2) li:hover:before {
  color: #00ADFF; }

ul.main-menu {
  margin: 0;
  display: inline-block;
  padding: 7px 0;
  list-style: none; }

ul.main-menu li {
  display: inline-block; }

.menu-wrap-2:first-child ul.main-menu > li {
  height: 18px;
  margin-right: 2px;
  padding-right: 6px;
  border-right: 1px solid; }

.mob-menu.menu-1 ul.main-menu li {
  height: 18px;
  margin-right: 2px; }

li.current-menu-item > a,
li.current-page-ancestor > a {
  color: #00ADFF; }

li.current-menu-item:before,
li.current-page-ancestor:before {
  color: #00ADFF; }

ul.sub-menu li:before {
  display: none; }

ul:not(.side-menu) ul.sub-menu {
  display: none;
  position: absolute;
  padding: 0;
  text-align: left;
  border: 1px solid;
  background: #fff; }

.menu-wrap-2:last-child ul.sub-menu {
  margin: 10px 0 0 20px; }

ul:not(.side-menu) ul.sub-menu a {
  display: block;
  padding: 3px 6px; }

ul:not(.side-menu) ul.sub-menu a:hover {
  color: #fff;
  background: #00ADFF; }

ul.sub-menu li {
  display: block; }

ul.sub-menu a {
  font-size: 15px; }

.menu-wrap-2:nth-child(2) ul {
  float: left; }

.menu-wrap-2:after {
  content: "";
  display: table;
  clear: both; }

.menu-wrap-2 form.searchform {
  display: inline-block;
  position: relative;
  top: -6px; }

.menu-wrap-2 label.screen-reader-text {
  display: none; }

.menu-wrap-2 form.searchform input[type="text"] {
  width: 120px;
  height: 13px; }

.menu-wrap-2 form.searchform input[type="submit"] {
  height: 19px;
  padding-top: 0; }

.show-menu-wrap {
  display: none;
  margin-bottom: 20px; }

.show-menu-wrap p {
  text-align: center;
  background: #00ADFF;
  color: #fff;
  margin: 0;
  padding: 9px 0;
  cursor: pointer; }

.show-menu {
  width: 0;
  height: 0;
  border-left: 30px solid transparent;
  border-right: 30px solid transparent;
  border-top: 30px solid #00ADFF;
  margin: auto;
  cursor: pointer; }

.header-menus-mob-wrap {
  display: none;
  padding-top: 10px;
  margin-bottom: 20px;
  border-bottom: 1px solid; }

.mob-menu.menu-1 {
  text-align: center;
  margin-top: -10px; }

.mob-menu.menu-2 {
  padding-left: 20px; }

.mob-menu.menu-2 li {
  display: block; }

.mob-menu ul.main-menu li.menu-item-has-children:hover ul.sub-menu {
  display: none; }

.logo-wraps {
  display: inline-block;
  margin-left: 40px;
  float: left; }

footer .logo-wraps {
  display: block; }

.divider {
  width: 10px;
  height: 60px;
  margin-top: -5px;
  margin-left: 10px;
  border-left: 1px solid #000;
  margin-bottom: 6px;
  float: left; }

a.logo {
  display: inline-block;
  width: 50px;
  height: 50px;
  margin: 0;
  float: left;
  font-size: 60px;
  text-decoration: none;
  color: #fff; }

a.logo.naukma-logo {
  height: 46.5px;
  background: url(../../img/naukma-logo.png) left center no-repeat;
  background-size: cover; }

a.logo.fcs-logo {
  background: url(../../img/fcs-logo.png) left center no-repeat;
  background-size: cover; }

a.logo.footer-logo {
  width: 210px;
  height: 60px;
  background: url(../../img/footer-logo.png) left center no-repeat;
  background-size: cover; }

a.logo.naukma-logo:hover,
a.logo.fcs-logo:hover {
  background-position: right center; }

h3 {
  font-size: 34px; }

h4 {
  font-size: 32px;
  margin: 30px auto; }

h5 {
  font-size: 26px; }

.news-block {
  display: inline-block;
  width: 100%;
  margin: auto;
  position: relative; }

.page-content {
  width: 80%;
  min-height: 100%;
  margin: auto;
  position: relative; }

/* .with-side-menu class is added via script.js */
.page-content.with-side-menu {
  width: 70%;
  margin-left: 300px;
  margin-top: -20px;
  padding-top: 20px; }

body.page-template-archive-teacher .page-content p,
body.page-template-archive-student .page-content p {
  margin: 0; }

.peoples-wrap {
  width: 100%; }

.people-block {
  display: inline-block;
  width: 30%;
  float: left;
  margin-bottom: 30px;
  margin-right: 3%;
  padding: 5px;
  box-sizing: border-box;
  border: 2px solid;
  text-align: left;
  font-size: 14px; }

.people-block p {
  display: block !important;
  margin: 0; }

.people-block p.people-name {
  font-size: 24px; }

.teacher-block p.teacher-department,
.student-block p.student-course {
  margin: 6px 0; }

.people-block img,
.student-block img {
  width: 100%;
  height: auto; }

.people-block:hover a {
  text-decoration: underline; }

.people-block a:hover {
  color: #404041; }

.people-top-wrap img {
  width: 300px;
  height: auto; }

.people-top-wrap > p {
  float: left; }

.people-top-wrap p,
.people-top-wrap .people-titles-wrap {
  display: inline-block; }

.people-top-wrap .people-titles-wrap {
  margin-left: 20px; }

.people-top-wrap .people-titles-wrap h1 {
  margin-top: 20px; }

.page-content a,
.news-piece-block a:not(.news-title) {
  font-style: italic; }

ul.side-menu a {
  font-style: normal; }

.page-content p a:hover,
.page-content ul a:hover {
  color: #00ADFF; }

.grading-scale-wrap {
  overflow: scroll; }

.page-content table {
  border-collapse: collapse; }

.page-content table td {
  border: 1px solid;
  padding: 6px; }

.news-block {
  display: inline-block; }

.show-side-menu-btn {
  display: none;
  width: 36px;
  height: 25px;
  position: absolute;
  left: -6%;
  top: -12px;
  cursor: pointer;
  background: url(../../img/show-side-menu-btn.png) no-repeat;
  background-size: cover; }

ul.side-menu {
  max-width: 230px;
  border-right: 1px solid #404041;
  background: #FBFBFB;
  position: absolute;
  height: 100%;
  left: -320px;
  list-style-type: none;
  top: -60px;
  padding-top: 90px;
  padding-right: 30px;
  z-index: 10; }

ul.side-menu li.current-menu-item > a {
  color: #00ADFF; }

.clearfix:after {
  content: '';
  display: table;
  clear: both; }

.news-wrap {
  display: block;
  padding: 0 3% 0 8%; }

.new-excerpt {
  font-size: 16px;
  margin: 0 auto 12px; }

.news-piece-block {
  display: block;
  /*width: 15%;*/
  width: 198px;
  text-align: left;
  margin-bottom: 20px;
  border: 2px solid;
  padding: 5px;
  box-sizing: box-sizing; }

.news-piece-block.wide {
  width: 420px; }

.news-piece-block img {
  display: block; }

.news-piece-block-text-wrapper a.news-title {
  /*border: 2px solid;
    padding: 4px 6px;*/
  font-weight: bold; }

.news-piece-block-text-wrapper {
  overflow: auto; }

body.home .news-piece-block:first-child {
  border: none; }

body.home .news-piece-block:first-child p {
  color: #00ADFF;
  font-size: 20px; }

.news-piece-block img {
  width: 100%;
  height: auto; }

body.single-new img {
  height: auto; }

body.single-new .page-content {
  overflow: auto; }

.back-to-news-link {
  text-decoration: underline; }

#googleMap {
  width: 700px;
  height: 216px;
  float: right; }

.social-wrap {
  display: inline-block;
  position: relative;
  margin: 0 10px 0 4px; }

.social-wrap:after {
  content: '';
  display: table;
  clear: both; }

.icon {
  width: 22px;
  height: 22px;
  float: left;
  padding: 0;
  font: 26px "Flaticon";
  transition: color 0.2s; }

.icon:before {
  position: relative;
  top: -3px; }

.icon:not(:last-child) {
  margin-right: 12px; }

.icon-fb:hover {
  color: #3B5998; }

.icon-twitter:hover {
  color: #1DA1F2; }

.icon-youtube:hover {
  color: #E62117; }

.icon-wiki:hover {
  color: #949494; }

@media (max-width: 1366px) {
  .news-wrap {
    display: block;
    padding: 0 3% 0 6%; } }

@media (max-width: 1076px) {
  footer {
    padding-bottom: 18px; }
  .footer-content-wrap {
    display: inline-block;
    margin-left: calc(50% - 285px); }
  footer .logo-wraps {
    margin-top: 26px; }
  .footer-text-wrap p {
    display: block;
    margin: 0;
    border-right: none; } }

@media (max-width: 1024px) {
  #googleMap {
    width: 100%;
    margin: 20px auto 40px; } }

@media (max-width: 994px) {
  header {
    margin-bottom: 0; }
  .show-menu-wrap {
    display: block; }
  .menu-wrap-2 ul.main-menu {
    display: none; }
  .menu-wrap-2:first-child {
    padding-top: 14px; }
  header .logo-wraps {
    margin: auto;
    margin-top: 14px;
    float: none; }
  body.page-template-page-about .page-content,
  body.page-template-archive-teacher .page-content,
  body.single-teacher .page-content,
  .page-content.with-side-menu {
    width: 86%;
    margin-left: 0;
    margin: auto; }
  ul.side-menu {
    display: none;
    position: absolute;
    height: auto;
    left: -6%;
    top: 0;
    padding: 0 10px;
    background-color: #4c4c4c;
    background-color: rgba(76, 76, 76, 0.82);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#99000000, endColorstr=#99000000);
    -ms-filter: "progid:DXImageTransform.Microsoft.gradient(startColorstr=#99000000, endColorstr=#99000000)";
    box-shadow: none; }
  ul.side-menu.sunken {
    top: 20px; }
  ul.side-menu li:not(.current-menu-item):not(.current-menu-ancestor) > a {
    color: #fff; }
  .show-side-menu-btn {
    display: block; }
  .show-side-menu-btn.sunken {
    top: 8px; } }

@media (max-width: 620px) {
  footer {
    text-align: center;
    padding-top: 0; }
  footer .logo-wraps {
    display: inline-block;
    float: none;
    margin: auto;
    margin-top: 20px; }
  .footer-text-wrap {
    float: none;
    margin: auto; } }

@media (max-width: 580px) {
  .teachers-3-wrap {
    text-align: center; }
  .teacher-block {
    width: 70%;
    float: none;
    margin-right: 0; } }

@media (max-width: 535px) {
  .news-wrap {
    padding: 0 6%; }
  .news-piece-block.normal,
  .news-piece-block.wide {
    width: 86%; } }
